import React, {Component} from 'react';
import './App.css'
import './bootstrap/css/bootstrap.css'

class Home_Page extends Component{
  state={
    title:[],
    content:[],
  }
  changeMenu=(text)=>{
    this.props.Home_Page(text)
  }
  make_file=()=>{
      var fs = require('fs');
      var file = 'test1.txt';
      fs.open(file,'w',function(err,fd){
         if (err) throw err; console.log('file open complete');
    });
  }
  add_post=(title,text)=>{
    this.props.add_post(title,text)
  }
  render(){

      return (
        <div className="Home_Page">

          <nav className="main_header">
            {this.props.state.gotoSite.map((site,key)=>{return <a className="button_css col-md-3" onClick={()=>{this.changeMenu(site)}}>{site}</a>})}
          </nav>
          <p className="body">
            저를 소개합니다.
            <br/>저는 충남대학교 컴퓨터공학과 이재용입니다.
            <br/>이 블로그는 제가 어떤 공부를 했는지 모여주는 블로그입니다.
            <br/>이 페이지는 공부했던 내용을 추가할 수 있습니다.
            <br/>
            <input className='title_box' id='title' placeholder="title"/><br/>
            <div className='content_box' contentEditable="true" id='content' placeholder="content"></div>
            <button className='button_box' onClick={()=>{{this.add_post(document.getElementById('title').value, document.getElementById('content').innerHTML)}}}>등록</button>
          </p>
        </div>
    );
  }
}

export default Home_Page;
